body {
  background-color: #f7f7ff;
  font-family: "Nunito", sans-serif;
}
.title {
  margin: auto;
  padding: auto;
  text-align: center;
}
.switch-container {
  display: flex;
  justify-content: center;
  vertical-align: center;
  font-weight: 600;
}
.switch {
  margin-top: 1.5vh;
  position: relative;
  width: 60px;
  height: 34px;
}
.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}
.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  -webkit-transition: 0.4s;
  transition: 0.4s;
  border-radius: 34px;
}
.slider:before {
  position: absolute;
  content: "";
  height: 26px;
  width: 26px;
  left: 4px;
  bottom: 4px;
  background-color: white;
  -webkit-transition: 0.4s;
  transition: 0.4s;
  border-radius: 50%;
}
input:checked + .slider {
  background-color: #7074df;
}
input:focus + .slider {
  box-shadow: 0 0 1px #7074df;
}
input:checked + .slider:before {
  -webkit-transform: translateX(26px);
  -ms-transform: translateX(26px);
  transform: translateX(26px);
}
